header {
    /* height: 100vh; */
    padding-top: 5rem;
    overflow: hidden;
    height: 790px;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*=============================== CTA ===============================*/

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*=============================== HEADER SOCIALS ===============================*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
  
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

