.me {
    background: linear-gradient(var(--color-primary),transparent);
    /* width: 22rem;
    height: 30rem; */
     width: 20rem;
    height: 28rem;
    position: absolute;
    left: calc(50% - 11rem); 
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0rem 0rem;
    overflow: hidden;
    padding: 4rem 0.0rem 0.0rem 0.0rem;
} 

/*=============================== SCROLL DOWN ===============================*/

.scroll__down {
    position: absolute;
    right: -4.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/*================================ MEDIA QUERIES (MEDIUM DEVICES) ===================================*/

@media screen and (max-width: 1024px) {
    header {
        /* height: 68vh; */
        height: 795px;
    }
    
}

/*================================ SMALL QUERIES (MEDIUM DEVICES) ===================================*/

@media screen and (max-width: 600px) {
    header {
        /* height: 100vh; */
        height: 790px;
    }
    .header__socials,.scroll__down {
        display: none;
    }

}

